import React, {useEffect, useState} from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import {useHistory, useParams} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

// styled : CSS를 미리 입힌 컴포넌트
let 박스 = styled.div`
  padding: 20px;
`;

let 제목 = styled.h4`
  font-size: 25px;
  color: ${(props) => props.색상};
`;

function Detail(props) {
  let history = useHistory();
  let {id} = useParams(); // Router의 Param 훅. /:id에 사용자가 입력한 값이 변수에 저장

  let [shoes, setShoes] = useState(props.shoes);

  const [showAlert, setShowAlert] = useState(true);

  const sho = shoes.find((item) => item.id === parseInt(id));
  // Ajax를 이용하여 데이터 추출 가능  ex) {id : 0}

  // 컴포넌트 렌더링 시, 업데이트 시 실행
  // 여러개 선언 가능. 코드 순서대로 실행
  useEffect(() => {
    // 특정시간이 지나서 코드 실행
    let timer = setTimeout(() => {
      // setTimeout : 시간이 지나기 전 페이지 이동 시 문제가 발생할 수 있음.
      setShowAlert(false);
    }, 2000);
    console.log('aaa');

    // 컴포넌트가 사라질 때 return 실행
    // return function func(() => {
    // });
    return () => {
      clearTimeout(timer); // 컴포넌트가 꺼질 때 타이머를 종료시켜 문제 제거
    };
  }, [showAlert]); //  showAlert(Array 내 객체)가 변경될 때만 해당 useEffect가 실행됨. 최초에 한번은 실행 되긴함
  //}, []); //  어떤 객체가 업데이트 되어도 최초 한 번 말고는 더 이상 실행이 되지 않음.

  let [temp, setTemp] = useState('');
  return (
    <Container>
      <박스>
        <제목 className="red">Detail</제목>
      </박스>
      {showAlert ? (
        <div className="my-alert-red">
          <p>재고가 얼마 남지않았습니다.</p>
        </div>
      ) : null}
      <Row>
        <Col>
          {temp}
          <input
            onChange={(e) => {
              setTemp(e.target.value);
            }}
          ></input>
          <img
            src={`https://codingapple1.github.io/shop/shoes${sho.id + 1}.jpg`}
            alt={sho.id}
            width="100%"
          />
          <h4>{sho.title}</h4>
          <p>{sho.content}</p>
          <p>{sho.price}KRW</p>
          <Info 재고={props.재고} index={id}></Info>
          <button
            className="btn btn-danger"
            onClick={() => {
              const temp = [...props.재고];
              temp[id]--;
              props.set재고(temp);
            }}
          >
            주문하기
          </button>
          <button
            className="btn btn-danger"
            onClick={() => {
              history.goBack(); // 뒤로가기
              // history.pust("/some/any"); // 특정경로로 이동
            }}
          >
            뒤로가기
          </button>
        </Col>
      </Row>
    </Container>
  );
}

function Info(props) {
  return <p>재고 : {props.재고[props.index]}</p>;
}

export default Detail;
